import React from "react"
import { Formik, Form, Field, ErrorMessage } from "formik"

import styles from "../styles/checkout.module.css"
import * as Yup from "yup"
import Buy from "../components/Buy"
import Layout from "../components/layout"
import CartList from "../components/CartList"

function Checkout() {
    
  const Validation = Yup.object().shape({
    firstName: Yup.string().required("Required"),
    lastName: Yup.string().required("Required"),
    email: Yup.string().email("Invalid Email").required("Required"),
    Pnumber: Yup.string().required("Required").min(10).max(10),
    Add1: Yup.string().required("Required"),
    landMark: Yup.string().required("Required"),
  })

  return (
    <Layout title="checkout">
      <div className={styles.container}>
        <Formik
          initialValues={{
            firstName: "",
            lastName: "",
            email: "",
            Pnumber: "",
            Add1: "",
            Add2: "",
            Add3: "",
            landMark: "",
          }}
          validationSchema={Validation}
          onSubmit={(values, { setSubmitting }) => {
            setTimeout(() => {
              alert(JSON.stringify(values, null, 2))
              setSubmitting(false)
            }, 400)
          }}
        >
          {({ isSubmitting }) => {
            return (
              <Form className={styles.form}>
                <div className={styles.formRow}>
                  <div>
                    <label className={styles.formLabel} htmlFor="firstName">
                      First Name
                    </label>
                    <Field
                      className={styles.formField}
                      placeholder="Jane"
                      type="text"
                      name="firstName"
                    />
                    <ErrorMessage
                      className={styles.formError}
                      name="firstName"
                      component="div"
                    />
                  </div>
                  <div>
                    <label className={styles.formLabel} htmlFor="lastName">
                      Last Name
                    </label>
                    <Field
                      className={styles.formField}
                      placeholder="Doe"
                      type="text"
                      name="lastName"
                    />
                    <ErrorMessage
                      className={styles.formError}
                      name="lastName"
                      component="div"
                    />
                  </div>
                </div>

                <div className={styles.formRow}>
                  <div>
                    <label className={styles.formLabel} htmlFor="Pnumber">
                      Phone Number
                    </label>
                    <Field
                      className={styles.formField}
                      placeholder="9008755620"
                      type="text"
                      name="Pnumber"
                    />
                    <ErrorMessage
                      className={styles.formError}
                      name="Pnumber"
                      component="div"
                    />
                  </div>

                  <div>
                    <label className={styles.formLabel} htmlFor="email">
                      Email
                    </label>
                    <Field
                      className={styles.formField}
                      placeholder="jane@example.com"
                      type="email"
                      name="email"
                    />
                    <ErrorMessage
                      className={styles.formError}
                      name="email"
                      component="div"
                    />
                  </div>
                </div>

                <label className={styles.formLabel} htmlFor="Add1">
                  Address Line 1
                </label>
                <Field
                  className={styles.formField}
                  placeholder="Second floor, Example apartment"
                  type="text"
                  name="Add1"
                />
                <ErrorMessage
                  className={styles.formError}
                  name="Add1"
                  component="div"
                />

                <label className={styles.formLabel} htmlFor="Add2">
                  Address Line 2
                </label>
                <Field
                  className={styles.formField}
                  placeholder="#2 hutchins road"
                  type="text"
                  name="Add2"
                />

                <label className={styles.formLabel} htmlFor="Add3">
                  Address Line 3
                </label>
                <Field
                  className={styles.formField}
                  placeholder="Cox town"
                  type="text"
                  name="Add3"
                />

                <label className={styles.formLabel} htmlFor="landMark">
                  Landmark
                </label>
                <Field
                  className={styles.formField}
                  placeholder="Opposite Mantri Square"
                  type="text"
                  name="landMark"
                />
                <ErrorMessage
                  className={styles.formError}
                  name="landMark"
                  component="div"
                />

                <button className={styles.button} type="submit">Submit</button>
              </Form>
            )
          }}
        </Formik>
        <div>
          <CartList />
        </div>
      </div>
    </Layout>
  )
}

export default Checkout
